@import "../helpers/variables";
@import "../helpers/mixins";
//first section
.campaign {
    padding-bottom: 22px;
    background-color: $main-color;
    color: $font-color;
    .twoColumns {
        align-items: center;
        p {
            margin-bottom: 31px;
            margin-right: 110px;
            @media only screen and (max-width: 660px) {
                margin-bottom: 16px;
                margin-right: 0; 
            }
        }
        .stylishCornerTreeColor:has(.btn) {
            margin-bottom: 101px;
            @media only screen and (max-width: 960px) {
                margin-bottom: 20px;
            }
        }
    }
    .imageHolder {
        aspect-ratio: 563/618;
        max-width: 578px;
        border: 0;
        border-right: 24px;
        border-style: solid;
        border-image: linear-gradient($orange-color 12%, $orange-light-color 12%, $orange-light-color 47%, $blue-color 47%, $blue-color 54%, transparent 54%, transparent 100%) 1;
        padding-left: 16px;
        @media only screen and (max-width: 1350px) {
            max-width: 50%;
        }
        @media only screen and (max-width: 960px) {
                max-width: 100%;
                width: 100%;
                align-self: center;
                max-height: 450px;
                position: relative;
                border: 0;
                padding: 0;
                &::before { 
                    content:'';
                    width: 16px;
                    height: 75px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    @include colorlineVar2(bottom);
                }
                &::after { 
                    content:'';
                    width: 25px;
                    height: 225px;
                    position: absolute;
                    right: 0;
                    top: 0;
                    @include colorlineVar2(bottom);
                }
        }
        @media only screen and (max-width: 660px) {
            &::before, &::after {
                display: none;
            }
        }
        img {
            display: block;
            border: 0;
            border-left: 16px;
            border-style: solid;
            border-image: linear-gradient(transparent 84%, $orange-color 84%, $orange-color 88%, $orange-light-color 88%, $orange-light-color 98%, $blue-color 98%, $blue-color 100%) 1;
            @media only screen and (max-width: 960px) {
                border: 0;
            }
        }
    }
    .clientLists {
        padding-top: 39px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 60px;
        font-weight: 500;
        @media only screen and (max-width: 1020px) {
            gap: 20px;
        }
        @media only screen and (max-width: 768px) {
            flex-direction: column;
            gap: 0;
            align-items: flex-start;
        }
        @media only screen and (max-width: 660px) {
            align-items: center;
            text-align: center;
        }
        span {
            @include font(14, 20);
            margin-bottom: 15px;
        }
        p {
            @include font(18, 28);
            color: $white-color;
        }
    }
}
.logosList {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    @media only screen and (max-width: 1020px) {
        gap: 20px;
    }
    @media only screen and (max-width: 768px) {
        width: 100%;
    }
    @media only screen and (max-width: 660px) {
        flex-wrap: wrap;
        justify-content: center;
        & > li {
            width: calc(50% - 10px);
        }
    }
    svg {
        width: 100px;
        height: auto;
        display: block;
        @media only screen and (max-width: 960px) {
            width: 76px;
        }
        @media only screen and (max-width: 660px) {
            margin: 0 auto;
        }
    }
    a:hover svg path {
        fill: $white-color; 
        &.whiteColor {
            fill: $grey-dark-color;
        }
    }
}
//section about us
.aboutUs {
    padding-top: 120px;
    @media only screen and (max-width: 1020px) {
        padding-top: $main-space-tb;
    }
    .twoColumns {
        .textSide {
            padding: 44px 0 47px 90px;
            @media only screen and (max-width: 960px) {
                padding: 0 0 20px;
            }
            @media only screen and (max-width: 660px) {
                padding: 0;
            }
        }
    }
    .twoColumns > .counterList {
        @media only screen and (max-width: 1020px) {
            max-width: 100%;
            justify-content: flex-start;
            gap: 20px;
        }
        @media only screen and (max-width: 660px) {
            flex-wrap: wrap;
            & > div {
                width: calc(50% - 10px);
            }
        }
    }
    h3 {
        margin-bottom: 16px;
    }
    .threeImgBoxes {
        margin-bottom: 48px;
        @media only screen and (max-width: 960px) {
            margin-bottom: 20px;
            .imageHolder {
                max-width: calc((100% - 30px) / 3);
            }
        }
    }
    .arrowLink {
        align-self: center;
        margin-top: 13px;
        @media only screen and (max-width: 960px) {
            align-self: flex-end;
        }
        &::after {
            width: 24px;

        }
    }
}
//section approach
.approach {
    align-items: center;
    .verticalListBox {
        padding-top: 16px;
        max-width: 515px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 12px;
        @media only screen and (max-width: 1200px) {
            max-width: 45%;
        }
        @media only screen and (max-width: 960px) {
            max-width: 100%;
        }
        &::after {
            content: ' ';
            position: relative;
            left: 0;
            bottom: 12px;
            height: 8px;
            width: 100%;
            @include colorline(right);
            display: block;

        }
        .item {
            padding: 36px 20px 4px 32px;
            color: $font-color2;
            background-color: $white-color;
            display: flex;
            gap: 16px;
            @media only screen and (max-width: 960px) {
                padding: 25px 20px 10px 25px;
            }
            .titleItem {
                color: $black-color;
                font-weight: 600;
                margin-bottom: 5px;
            }
            svg {
                width: 47px;
                height: 47px;
                display: block;
                flex: 1 0 auto;
                @media only screen and (max-width: 960px) {
                    flex: 0 1;
                    min-width: 47px;
                }
            }
        }
    }
}
// section process
.nubmerListBox {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 47px;
    overflow: hidden;
    padding-top: 40px;
    @media only screen and (max-width: 768px) {
        flex-direction: column;
        padding-top: 0;
        margin-top: 20px;
        align-items: center;
    }
    @media only screen and (max-width: 660px) {
        gap: 35px;
    }
    & >  li {
       height: 164px;
       border: 1px solid $grey-color;
       position: relative;
       padding: 30px 30px 30px 95px;
       color: $font-color-grey;
       background-color: $white-color;
       flex: 1 0 100%;
       max-width: 395px;
        @media only screen and (max-width: 1350px) {
            max-width: calc((100% - 94px) / 3);
            height: auto;
        }    
        @media only screen and (max-width: 960px) {
            max-width: calc((100% - 94px) / 2);
        }
        @media only screen and (max-width: 768px) {
            max-width: 80%;
        }
        @media only screen and (max-width: 660px) {
            max-width: 100%;
            padding: 30px 30px 0px 60px;
        }
       &::before {
            content: ' ';
            height: 2px;
            width: 50%;
            position: absolute;
            top: calc(50% - 2px);
            left: 0;
            transform: translateX(-100%);
            border-top: 2px dashed $grey-color;
            z-index: -1;
            @media only screen and (max-width: 768px) {
                height: 50%;
                width: 2px;
                position: absolute;
                top: 0;
                left: calc(50% - 2px);
                transform: translate(0, -100%);
                border-left: 2px dashed $grey-color;
            }
       }
       &:last-child, &:first-child {
            &::after {
                content: ' ';
                position: absolute;
                left: -1px;
                top: -9px;
                height: 8px;
                width: calc(100% + 2px);
                @include colorline(right);
            }
       }
       &:first-child {
            &::after {
                bottom: -9px;
                top: auto;
            }
       }
       .textHolder {
        position: relative;
            &::after {
            content: ' ';
            @include circle(47);
            position: absolute;
            left: -63px;
            top: 2px;
            background: #FFF8F1 url(./../../images/sprite.png) 15px 10px no-repeat;
            @media only screen and (max-width: 660px) {
                left: -53px;
            }
        }
        &.Designing {
            &::after {
                background-position: 14px -28px;
            }
        }
        &.Development {
            &::after {
                background-position: 12px -77px;
            }
        }
        &.Testing {
            &::after {
                background-position: 11px -118px;
            }
        }
        &.Deployment {
            &::after {
                background-position: 12px -162px;
            }
        }
        &.Maintenance {
            &::after {
                background-position: 12px -212px;
            }
        }
    }
    .number {
        position: absolute;
        top: 11px;
        right: 14px;
        font-weight: 600;
        color: $font-color-lightest;
        @include font(24, 32);
       }
    }
}
// section review
.listSmallImage {
    display: flex;
    gap: 12px;
    margin-bottom: 14px;
}
.commentsCustomer {
    gap: 65px;
    padding-top: 7px;    
    @media only screen and (max-width: 960px) {
        gap: 20px;
    }
    > div {
        flex: 1 0;
    }
}
.slideHolder50 {
    position: relative;
    margin-top: 13px;
    &::before {
        content: ' ';
        position: absolute;
        left: -17px;
        top: 0;
        height: 340px;
        width: 17px;
        @include colorlineVar2(bottom);
        @media only screen and (max-width: 1200px) {
            height: 420px;
        }
        @media only screen and (max-width: 960px) {
            height: 360px;
        }
    }
    &::after {
        content: ' ';
        position: absolute;
        right: 39px;
        top: -19px;
        width: 43px;
        height: 30px;
        background: url('./../../images/quote.svg') 0 0 no-repeat;
    }
    .slide {
        height: 340px;
        overflow: hidden;
        @media only screen and (max-width: 1200px) {
            height: 420px;
        }
        @media only screen and (max-width: 960px) {
            height: 360px;
        }
        background-color: $white-color;
        position: relative;
        padding: 48px 65px 42px 40px;
        @media only screen and (max-width: 660px) {
            padding: 20px 20px 20px 25px;
        }
        > div {
            height: 100%;
            display: flex;
            flex-direction: column;
        }
        blockquote {
            flex-grow: 1;
        }
        .infoClient {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-weight: 500;
            @include font(14, 20);
            color: $font-color-grey;
            .personalInfo {
                flex-grow: 1;
            }
            span {
                display: block;
                &.name {
                    font-weight: 500;
                    color: $main-color;
                    @include font(16, 24);
                }
            }
            img {
                float: left;
                margin-right: 18px;
                margin-top: 5px;
                @include circle(38);
            }
            svg {
                display: block;
                height: auto;
                width: 100px;
            }
        }
    }
}
//latest news
.latersNews {
    .twoColumns {
        gap: 32px;
        padding-top: 31px;
        @media only screen and (max-width: 960px) {
            padding-top: 0;
        }
        & > div {
            flex: 1 0;
            @media only screen and (max-width: 480px) {
                flex-direction: column;
                .imgHolder {
                    max-width: 100%;
                }
            }
        }
    }
}