.titleSectionHolder {
    width: 61%;
    padding-bottom: 1px;
    @media only screen and (max-width: 960px) {
        width: 100%;
    }
    &.subSection {
        width: 49%;
        @media only screen and (max-width: 960px) {
            width: 100%;
        }
    }
    .stylishCornerTreeColor {
        margin-top: 35px;
        @media only screen and (max-width: 960px) {
            margin-top: 0;
        }
    }
}
//slider
.sliderItems {
    padding-top: 80px;
    @media only screen and (max-width: 960px) {
        padding-top: 50px;
    }
    .slick-slider {
        margin: 0 -15px;
    }
    .slick-slide {
        padding: 0 16px;
    }
    .item {
        padding: 48px 22px 44px 48px;
        color: $font-color2;
        background-color: $white-color;
        @media only screen and (max-width: 1200px) {
            padding-left: 22px;
        }
        @media only screen and (max-width: 960px) {
            padding: 25px 22px 25px;
        }
        .titleItem {
            color: $black-color;
            font-weight: 600;
            margin-bottom: 9px;
            & ~ p {
                height: 96px;
                overflow: hidden;
            }
        }
        .arrowLink {
            padding-right: 35px;
        }
        svg {
            width: 47px;
            height: 47px;
            display: block;
            margin-bottom: 20px;
        }
    }
    /* arrow slider */
    .prev-btn, .next-btn {
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        @include circle(30);
        transform: translateY(-50%);
        cursor: pointer;
        border: 0;
        outline: none;
        background: $blue-lightest-color;
        opacity: 0.6;
        z-index: 1;
        svg {
            width: 30px;
            height: 30px;
            display: block;
            circle {
                stroke: $main-color;
            }
            path {
                fill: $main-color;
            }
        }
        &:hover {
            opacity: 1;
            transform: translateY(-50%) scale(1.1);
            box-shadow: 0 0 10px $blue-lightest-color;
        }
    }
    .next-btn {
        left: auto;
        right: 0;
    }
}
section .slick-dots {
    text-align: center;
    bottom: -40px;
    li {
        width: 15px;
        height: 15px;
        margin: 0 15px;
        button {
            &::before {
                @include circle(15);
                position: absolute;
                top: 0;
                left: 0;
                width: 15px;
                height: 15px;
                content: '';
                background: $blue-lightest-color;
            }
        }
        &.slick-active {
            button::before {
                background: $blue-color;
            }
        }
    }
}
//subscribe
.subScribe {
    padding-bottom: $main-space;
    @media only screen and (max-width: 1020px) {
        padding-bottom: $main-space-tb;
    }
    &.stylishCornerTreeColor::after, &.stylishCornerTreeColor::before {
        @media only screen and (max-width: 660px) {
            display: none;
        }
    }
    .rightColorFullLine {
        &::after {
            width: 23px;
            @media only screen and (max-width: 660px) {
                display: none;
            }
        }
    }
    &::after {
        width: 89px;
        height: 51px;
        border-top-width: 30px;
        border-image: linear-gradient(to right, #FFA155 27%, #FFD3AF 27%, #FFD3AF 64%, #a3a7ff 64%, #a3a7ff 100%) 1;
        
    }
    &::before {
        width: 31px;
        height: 30px;
        top: 51px;
        background-color: #a3a7ff;
    }
    > div {
        background-color: $vialet-color;
        color: $white-color;
        height: 333px;
        padding: 0 $main-space;
        align-items: center;
        @media only screen and (max-width: 1020px) {
            padding: 0 $main-space-tb;
        }
        @media only screen and (max-width: 960px) {
            height: auto;
            padding: 40px 80px 20px;
            align-items: flex-start;
        }
        @media only screen and (max-width: 660px) {
            padding: 20px;
        }
    }
    form {
        width: 100%;
        margin: 10px 21px 0 65px;
        display: flex;
        flex-direction: column;
        position: relative;
        .btnHolder {
            align-self: flex-end;
        }
        .input-error {
            bottom: -16px;
            letter-spacing: 1px;
        }
        .fildSection label {
            left: 30px;
            top: 20px;
            @media only screen and (max-width: 480px) {
                left: 15px;
            }
        }
        .fildSection input:focus + label,
        .fildSection input:not(:placeholder-shown) + label {
            display: none;
        }
        @media only screen and (max-width: 960px) {
            margin: 0;
        }
        input {
            border: 0;
            width: 100%;
            padding: 20px 130px 20px 30px;
            height: 65px;
            font-family: $base-font;
            font-weight: 500;
            color: $font-color;
            @include font(16, 24);
            @media only screen and (max-width: 1200px) {
               padding: 20px 85px 20px 15px;
            }
            @media only screen and (max-width: 960px) {
                padding: 20px 130px 20px 30px;
            }
            @media only screen and (max-width: 480px) {
                padding: 20px 85px 20px 15px;
            }
        }
        .btnHolder {
            position: absolute;
            right: 0;
            top: 0;
            .btn {
                padding: 0 40px;
                height: 65px;
                @media only screen and (max-width: 1200px) {
                    padding: 0 20px;
                }
                @media only screen and (max-width: 960px) {
                    padding: 0 40px;
                }
                @media only screen and (max-width: 480px) {
                    padding: 0 20px;
                }
            }
        }
    }
}

//career sub page
.positionFirstSection {
    display: flex;
    justify-content: space-between;
    @media only screen and (max-width: 480px) {
        flex-direction: column;
    }
    .btn {
        display: inline-flex;
    }
}
.colorTeaser {
    background-color: #FFE0C7;
    color: $black-color;
    padding: 82px 110px 60px 80px;
    @media only screen and (max-width: 960px) {
        margin-left: 30px;
        padding: 30px 25px 10px 30px;
    }
    @media only screen and (max-width: 480px) {
        margin: 20px 0 0;
    }
    ul {
        padding-top: 5px;
        li {
            padding-bottom: 16px;
        }
    }
}
#tabContainer {
    margin-top: $main-space;
    color: $black-color;
    @media only screen and (max-width: 1020px) {
        margin-top: $main-space-tb;
    }
    .tabTitle {
        border-bottom: 4px solid #E0F1F2;
        display: flex;
        justify-content: center;
        gap: 96px;
        @media only screen and (max-width: 960px) {
            gap: 25px;
        }
        @media only screen and (max-width: 480px) {
            gap: 5px;
        }
        @media only screen and (max-width: 400px) {
            flex-direction: column;
            padding: 0 20px;
        }
        li {
            padding: 44px 0 29px;
            cursor: pointer;
            font-weight: 600;
            @include font(24, 36);
            @media only screen and (max-width: 960px) {
                padding: 25px 0;
                @include font(18, 24);
            }
            &.active, &:hover {
                color: $blue-color-font;
                border-bottom: 4px solid $blue-color-font;
                margin-bottom: -4px;
            }

        }
    }
    .tabContent {
        padding: 97px 0 70px;
        margin: 0 auto;
        width: 66%;
        @media only screen and (max-width: 960px) {
            padding: 40px 0 20px;
            width: 75%;
        }
        > div {
            display: none;
            &.active {
                display: block;
            }
        }
        ul li {
            list-style-type: disc;
            margin-bottom: 24px;
        }
    }
}
.applyNow {
    padding: $main-space 0 0;
    @media only screen and (max-width: 1020px) {
        padding: $main-space-tb 0 0;
    }
    h3 {
        margin-bottom: 30px;
    }
    form {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 32px;
        .imputHolder {
            width: calc(50% - 16px);
            position: relative;
            @media only screen and (max-width: 768px) {
                width: 100%;
            }
        }
        .textareaHolder, .checkboxHolder {
            width: 100%;
            position: relative;
        }
        input, textarea {
            border: 0;
            background: $vialet-lightest-color;
            color: #4e4f5d;
            padding: 42px 40px 30px;
            margin-bottom: 0;
        }
        textarea {
            height: 100px;
            height: 225px;
            + label {
                @include font(18, 28);
                font-weight: 500;
                position: absolute;
                left: 40px;
                top: 36px;
            }
        }
        label  {
            @include font(18, 28);
            font-weight: 500;
            position: absolute;
            left: 40px;
            top: 50%;
            transform: translateY(-50%);

        }
        input:focus + label, textarea:focus + label, input:not(:placeholder-shown) + label,
        textarea:not(:placeholder-shown) + label {
            top: 22px;
            @include font(16, 26);
        }
        .input-error {
            @include font(16, 26);
            letter-spacing: 0.6px;
            left: 40px;
            bottom: -50px;
            @media only screen and (max-width: 768px) {
                bottom: -36px;
            }
        }
        .stylishCornerTreeColor {
            margin-top: 13px;
        }
    }
}


