    .sotial-links {
        padding-top: 5px;
        justify-content: center;
    }
}
.peoplePage {
    margin-bottom: $main-space;
    @media only screen and (max-width: 1020px) {
        margin-bottom: $main-space-tb;
    }
}
//private polici
