    @media only screen and (max-width: 1020px) {
        padding: 50px 0;
    }
    &:after {
        content: ' ';
        position: absolute;
        left: 0;
        bottom: -24px;
        width: 296px;
        height: 24px;
        @include colorlineVar2(left)
    }
}
#privatPolicy {
    margin-bottom: 24px;
    .pixelOrange::before {
        width: 24px;
        height: 24px;
    }
    .titleSectionHolder.pixelOrange {
        padding-top: 50px;
        margin-bottom: 46px;
        &::before {
